import React, { useState, useRef, useEffect } from 'react';
import YouTube from 'react-youtube';

export function TrackPlayer({ queueList, setQueueList, updateCurrentTrack }) {
  const [currentTrack, setCurrentTrack] = useState(null);  // State for current track
  const nextTrackRef = useRef(null);

  // Function to update the queue and determine the next track to be played
  const updateQueueAndNextTrack = () => {
    if (queueList.length < 1) {
      if(currentTrack[0].trackId == 'fxU8dKWIZYM')
        nextTrackRef.current = 
        [
          {
            name: "Don't Stop Me Now",
            artist: 'Queen',
            length: '3:30',
            image: 'https://lh3.googleusercontent.com/1OCPTD9Vomtm5Hqt3d0-naampTDQVUix7EgeCWX0rHKRrD4_z70cZ-65Pn3pWWs6ME1F-lxK4ELlfhgy=w60-h60-l90-rj',
            trackId: 'QnkKrFKaqW4'
          },
          [ 2 ],
          [],
          false,
          false
        ];
      else{
        nextTrackRef.current = 
        [
          {
            name: "Don't Stop Me Now",
            artist: 'Queen',
            length: '3:30',
            image: 'https://lh3.googleusercontent.com/1OCPTD9Vomtm5Hqt3d0-naampTDQVUix7EgeCWX0rHKRrD4_z70cZ-65Pn3pWWs6ME1F-lxK4ELlfhgy=w60-h60-l90-rj',
            trackId: 'fxU8dKWIZYM'
          },
          [ 2 ],
          [],
          false,
          false
        ];
      }

    }
    else {
      let updatedQueue = [...queueList]; // Copy the queue to avoid direct mutation

      console.log(updatedQueue); // Log the next track
      for (let index = 0; index < updatedQueue.length && !nextTrackRef.current; index++) {
        const item = updatedQueue[index];

        // If votes disagree more than agree, skip this track
        if (item[1].length < item[2].length) {
          item[3] = true;
          item[4] = false;
          continue;
        } else {
          item[3] = true;
          item[4] = true;
          nextTrackRef.current = item;  // Set the next track reference
          console.log(item); // Log the next track
        }

        updatedQueue[index] = item; // Update the item in the copied queue
        break;
      }
      setQueueList(updatedQueue);
    }
  };

  // Function to handle when the video is about to finish (20 seconds left)
  const handleTimeUpdate = (event) => {
    if (event.data === window.YT.PlayerState.PLAYING) {
      // Start tracking progress once the video starts playing
      const currentTrackInterval = setInterval(() => {
        updateCurrentTrack(currentTrack)
      }, 5000);

      const interval = setInterval(() => {
        const player = event.target;
        const currentTime = player.getCurrentTime();
        const duration = player.getDuration();
        console.log(duration)
        if (duration - currentTime <= 20 && !nextTrackRef.current) {
          // Set next track reference when 20 seconds remain
          console.log('updating')
          updateQueueAndNextTrack();
          console.log(queueList)
        }
        // console.log(nextTrackRef.current)
      }, 100); // Update every 100 ms, but only trigger logic once

      // Clean up when the video is paused or ended
      event.target.addEventListener('onStateChange', (e) => {
        if (e.data === window.YT.PlayerState.PAUSED || e.data === window.YT.PlayerState.ENDED) {
          clearInterval(interval);
          clearInterval(currentTrackInterval);
        }
      });
    };
  }

  // Function to handle video end event
  const handleVideoEnd = () => {
    if (nextTrackRef.current) {
      // Set currentTrack to nextTrackRef on video end
      
      setCurrentTrack(nextTrackRef.current);
      updateCurrentTrack(nextTrackRef.current);

      // Find the next track's index in the queue and update
      // const nextTrackIndex = queueList.findIndex(item => item.trackId === nextTrackRef.current.trackId);

      // Reset nextTrackRef after updating the current track
      nextTrackRef.current = null;

      let removeCount = 0;
      for (let i = 0; i < queueList.length; i++) {
        if (queueList[i][3] == true) {
          removeCount++;
        }
      }
      for (let i = 0; i < removeCount; i++) {
        queueList.shift();
      }

      console.log(queueList);

      // Trigger re-render with the updated current track
      setQueueList(queueList);
    }
    else {
      setCurrentTrack(null);
      setQueueList([]);
    }
    console.log("end" + queueList)
  };

  // YouTube player options
  const opts = {
    height: '390',
    width: '640',
    playerVars: {
      autoplay: 1, // Automatically play the video
    },
  };

  // We start playing the first track from the queue when queueList has at least one track
  useEffect(() => {
    if (queueList && queueList.length > 0 && currentTrack === null) {
      setCurrentTrack(queueList[0]); // Set the first track in the queue as the current track
      queueList.shift();
      setQueueList(queueList);
    }
  }, [queueList, currentTrack]);  // Re-run when either queueList or currentTrack changes

  return (
    <div>
      {currentTrack ? (  // Check if currentTrack is not null
        <YouTube
          videoId={currentTrack[0].trackId} // Use the current videoId from the queue
          opts={opts}
          onEnd={handleVideoEnd} // Call handleVideoEnd when the video ends
          onStateChange={handleTimeUpdate} // Listen for time updates to check if 20s remain
        />
      ) : (
        <div>Loading...</div>
      )}
    </div>
  );
}
