import socketIOClient from "socket.io-client";
import API_BASE_URL from "../config.js";

let socket;

const connect = (shopId, token, onAuthenticated) => {
  if (!socket) {
    socket = socketIOClient(API_BASE_URL, {
      transports: ["websocket"],
      reconnection: true,
      reconnectionAttempts: Infinity,
      reconnectionDelay: 1000,
      reconnectionDelayMax: 5000,
      timeout: 20000,
      query: { shopId, token }, // Pass shopId and token as query parameters
    });

    socket.on("connect", () => {
      console.log(`Connected to server with shopId: ${shopId} and token: ${token}`);
      
      // Emit shopId and token to the server after connection
      socket.emit("authenticate", { shopId, token });
    });

    // Listen for authentication response from server
    socket.on("authenticated", (response) => {
      console.log('Authentication response:', response);
      onAuthenticated(true); // Call the callback function with authentication result
    });
    
    socket.on("claimPlayerRes", (response) => {
      console.log('Authentication response:', response);
      onAuthenticated(false); // Call the callback function with authentication result
    });
  }

  return socket;
};

const disconnect = () => {
  if (socket) {
    socket.disconnect();
    socket = null;
  }
};

export { connect, disconnect };
