import React, { useState, useEffect } from 'react';
import styles from './Player.module.css'; // Import the module.css file
import { ColorRing } from "react-loader-spinner";

const LinktreePage = ({ authenticating, authenticated }) => {
  return (
    <div className={styles.linktreePage}>


<div className={styles.dashboardLine}></div>
      <div className={styles.dashboardContainer}>
        {/* Main Heading */}
        <div className={styles.mainHeading}>
          <span>
            <p style={{ marginBottom: 0 }}>INI ADALAH PEMUTAR LAGU DARI</p>
            <p style={{ fontSize: '14vw', marginTop: 0 }}>KEDAIMASTER</p>
          </span>
          {!authenticated ? (
            authenticating ? (
              <>
                <p style={{ fontSize: '40px', marginTop: 0 }}>mengautentikasi</p>
                <ColorRing
                  visible={true}
                  height="80"
                  width="80"
                  ariaLabel="color-ring-loading"
                  wrapperStyle={{}}
                  wrapperClass="color-ring-wrapper"
                  colors={['#7f7f7f']}
                />
              </>
            ) : (
              <p style={{ fontSize: '30px', marginTop: 0 }}>anda tidak memiliki akses</p>
            )
          ) : (
            <p style={{fontSize: '40px', marginTop: 0}}>SEDANG DIPUTAR</p>
          )}

        </div>
            <div className={styles.footer}>
              <div className={styles.footerLinks}>
                <a href="https://linktr.ee/discover/trending" target="_blank" rel="noreferrer" className={styles.footerLink}>Pelajari lebih lanjut</a>
                <a href="https://linktr.ee" target="_blank" rel="noreferrer" className={styles.footerLink}>Tentang kedaimaster.com</a>
                
              </div>
            </div>
      </div>

    </div>
  );
};

export default LinktreePage;
