import React, { useEffect, useState, useRef } from 'react';

import { connect, disconnect } from './services/socketService'; // Adjust the path as necessary
import { TrackPlayer } from "./TrackPlayer";
import Player from "./Player";
import Modal from './Modal';

const App = () => {
  const [queue, setQueue] = useState([]);
  const [shopId, setShopId] = useState('');
  const [token, setToken] = useState('');
  const [authenticated, setAuthenticated] = useState(false); // New state for authentication
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalContent, setModalContent] = useState(null);
  const [authenticating, setAuthenticating] = useState(false);
  const [socket, setSocket] = useState(null); // Manage socket connection as state
  const isOn = useRef(false);


  useEffect(() => {
    // Function to handle beforeunload event
    const handleBeforeUnload = (event) => {
      event.returnValue = ''; // This will trigger the browser's default message
    };

    window.addEventListener('beforeunload', handleBeforeUnload);

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, []);

  useEffect(() => {
    // Function to get shopId and token from the URL
    const extractUrlParams = () => {
      const url = new URL(window.location.href);
      const pathSegments = url.pathname.split('/');

      if (pathSegments.length > 1 && pathSegments[1]) {
        setShopId(pathSegments[1]); // Set shopId from URL
      }

      const tokenParam = url.searchParams.get('token');
      if (tokenParam) {
        setToken(tokenParam); // Set token from URL
      }
    };

    extractUrlParams();
  }, []);

  useEffect(() => {
    // Connect to the socket if both shopId and token are present
    if (shopId && token) {
      setAuthenticating(true);
      const socketConnection = connect(shopId, token, (isAuthenticated) => {
        setAuthenticated(isAuthenticated); // Update authentication status based on server response

        if (isAuthenticated) {
          setTimeout(() => {
            console.log('Authentication successful!');
            setAuthenticating(false);
            setAuthenticated(true);
            // Listen for the updateQueue event from the socket server
            socketConnection.on("updateQueue", (response) => {
              setQueue(response);
              console.log(response);
              console.log(isOn.current)
              if ((response == null || response.length < 1) && !isOn.current) {
                setModal('stepByStep');
              }
              isOn.current = true;
            });

          }, 2000); // 2 seconds delay (can be adjusted as needed)
        } else {
          setTimeout(() => {
            setAuthenticating(false);
            console.log('Authentication failed!');
          }, 2000); // 2 seconds delay (can be adjusted as needed)
        }
      });

      // Store socket in state for emitting events later
      setSocket(socketConnection);

      // Cleanup the socket connection on component unmount
      return () => {
        socketConnection.off("updateQueue");
        socketConnection.off("authenticated"); // Remove the authentication listener as well
        disconnect();
      };
    } else {
      console.log('shopId or token not found in the URL. Not connecting to socket.');
    }
  }, [shopId, token]); // Add shopId and token as dependencies


  const setModal = (content, params = {}) => {
    setIsModalOpen(true);
    setModalContent(content);
  };

  const editQueue = (editedQueue) => {
    if (socket) {
      socket.emit("editQueue", { shopId, token, editedQueue });
    } else {
      console.error("Socket is not available for emitting events");
    }
  };

  const updateCurrentTrack = (currentTrack) => {
    if (socket) {
      socket.emit("updateCurrentTrack", { shopId, token, currentTrack });
    } else {
      console.error("Socket is not available for emitting events");
    }
  };

  const next = () => {
    setIsModalOpen(false);

    setQueue([

      [
        {
          name: "Don't Stop Me Now",
          artist: 'Queen',
          length: '3:30',
          image: 'https://lh3.googleusercontent.com/1OCPTD9Vomtm5Hqt3d0-naampTDQVUix7EgeCWX0rHKRrD4_z70cZ-65Pn3pWWs6ME1F-lxK4ELlfhgy=w60-h60-l90-rj',
          trackId: 'fxU8dKWIZYM'
        },
        [2],
        [],
        false,
        false
      ]
    ]);

  }

  return (
    <div>
      <Player authenticating={authenticating} authenticated={authenticated} />
      {authenticated && (
        <TrackPlayer queueList={queue} setQueueList={editQueue} updateCurrentTrack={updateCurrentTrack} />
      )}

      <Modal
        isOpen={isModalOpen}
        modalContent={modalContent}
        next={next}
        onClose={next}
      />
    </div>
  );
};

export default App;

